.back {
  display: flex;
  align-items: center;
  justify-content: start;
  border: none;
  background: none;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-bottom: 3rem;
  text-decoration: none;
  color: black;
  width: 7rem;
}
.back_icon {
  width: 2rem;
  height: 1.8rem;
  margin-right: 0.5rem;
}
.back_text {
  font-size: 1.4rem;
}
.back:hover {
  transform: translateY(-0.1rem);
}
.back:active {
  transform: translateY(0.05rem);
}

