.card_field
  display: grid
  grid-template-columns: auto max-content max-content
  grid-column-gap: 1.2rem
  margin-bottom: 1.2rem

.avatar
  width: 3.2rem
  height: 3.2rem
  border-radius: 50rem

.tag
  display: flex
  align-items: center
  border-radius: 1.2rem
  background: #F1F5F9
  padding: .5rem 1rem
  &_icon
    width: 1.6rem
    height: 1.6rem
    margin-right: .5rem
  &_text
    font-size: 1rem
