.search {
  display: block;
  width: 100%;
  height: 4.8rem;
  border-radius: 1.2rem;
  background: white;
  border: none;
  padding: 1.6rem 2rem;
  font-size: 1.6rem;
  margin-bottom: 3rem;
}
@media (max-width: 750px) {
  .search {
    margin-bottom: 2rem;
  }
}
.search::placeholder {
  color: rgba(58, 58, 58, 0.32);
}
.search:focus {
  outline: rgba(58, 58, 58, 0.1) solid 1px;
}

