* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: "Noto Sans", sans-serif;
  background: rgba(58, 58, 58, 0.04);
}
@media (max-width: 600px) {
  html {
    font-size: 55%;
  }
}

.container {
  padding: 5rem 12rem;
  font-size: 2.2rem;
}
@media (max-width: 1000px) {
  .container {
    padding: 5rem 8rem;
  }
}
@media (max-width: 500px) {
  .container {
    padding: 2rem;
  }
}

